.showcase {
  padding-top: 93px;
  margin-bottom: 82px;

  @media (max-width: $tablet) {
    padding-top: 42px;
    margin-bottom: 30px;
  }

  @media (max-width: $mobile) {
    padding-top: 32px;
  }

  .page--nojs & {
    visibility: visible;
  }
}

.showcase__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 35px;

  @media (max-width: $tablet) {
    margin-bottom: 26px;
  }

  @media (max-width: $mobile) {
    margin-bottom: 16px;
  }
}

.showcase__header h2 {
  margin-bottom: 24px;

  @media (max-width: $tablet) {
    margin-bottom: 20px;
  }
}

.showcase__link-shop {
  @media (max-width: $tablet) {
    padding-bottom: 4px;
  }
}

.showcase__item {
  padding-top: 24px;
  min-height: 388px;

  @media (max-width: $tablet) {
    min-height: 444px;
  }

  @media (max-width: $mobile) {
    min-height: 256px;
  }
}

.showcase__image {
  max-height: 284px;

  @media (max-width: $tablet) {
    max-height: 340px;
  }

  @media (max-width: $mobile) {
    max-height: 136px;
  }
}
